import Vuex from "vuex";
import Vue from 'vue';

//    import

Vue.use(Vuex);

// store 
export default new Vuex.Store({
    state: {
        token: null,
    },
    //state   getter 
    getters: {
        isLogin(state){
            return state.token == null ? false : true ;
        }
    },
    //state 
    mutations: {  // commit    .
        setToken(state, _token){
            state.token = _token;
        }
    },
    // mutations    dispatch  .
    actions: { // dispatch    .
        setToken:({commit} , _token) => {
            commit('setToken' , _token);
        }
    },

})


